.team-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .team-list {
    gap: 50px;
  }
}

.team-item {
  justify-content: center;
  align-items: center;
  width: 270px;
  height: 270px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 1440px) {
  .team-item {
    width: 350px;
    height: 300px;
  }
}

.team-ring {
  z-index: -1;
  stroke: var(--color-blue-component);
  width: 160px;
  height: 160px;
  position: absolute;
}

.team_people {
  text-align: center;
  flex-direction: column;
  gap: 5px;
  display: flex;
  position: absolute;
  bottom: 5px;
}

.team_name {
  letter-spacing: .04em;
  color: var(----dark-theme-componemt);
  font-size: 15px;
  line-height: 1.1875;
}

@media screen and (min-width: 768px) {
  .team_name {
    font-size: 18px;
  }
}

.team_position {
  letter-spacing: .04em;
  color: rgba(17, 17, 17, .6);
  font-size: 13px;
  line-height: 1.125;
}

@media screen and (min-width: 768px) {
  .team_position {
    font-size: 16px;
  }
}

